import * as React from "react";
import {PureComponent} from "react";
import {SignUp} from "./SignUp";
import {parseScrobble, Scrobble} from "../scrobbler/Scrobble";
import {MostRecentScrobblesSection} from "./MostRecentScrobblesSection";

const firebase: any = (window as any).firebase;
const firestore = firebase.firestore();
const settings = {timestampsInSnapshots: true};
firestore.settings(settings);

export class Auth extends PureComponent<{
    username: string,
    email: string,
    uid: string,
    apiKey: string,
    token: string,
}, {
    signingIn: boolean;
    signUpOpen: boolean,
    username: string,
    email: string,
    uid: string,
    emailEntered: string,
    passwordEntered: string,
    paymentDetails?: any,
    mostRecentScrobbles: Scrobble[],
    apiKey: string,
    token: string,
    authorizing: boolean,
}> {
    state = this.getInitialS();

    getInitialS() {
        if (firebase) {
            firebase.auth().onAuthStateChanged((u?: any) => {
                if (!!u && u.email) {
                    console.log("Signed in as " + (u.displayName || u.email));
                    const uid = u.uid;
                    this.setState({
                        email: u.email,
                        username: u.displayName || u.email,
                        uid: uid,
                        signingIn: false,
                    });
                    // this.fetchMostRecentScrobbles(uid)
                    //     .then(scrobbles => this.setState({
                    //         mostRecentScrobbles: scrobbles,
                    //     }));
                } else {
                    this.setState({
                        email: "",
                        username: "",
                        uid: "",
                        signingIn: false,
                    });
                }
            });
            // todo listen for auth change
        }
        return {
            mostRecentScrobbles: [],
            signingIn: false,
            emailEntered: "",
            passwordEntered: "",
            username: this.props.username || "",
            email: this.props.email || "",
            uid: this.props.uid || "",
            paymentDetails: null,
            paymentComplete: null,
            signUpOpen: false,
            apiKey: this.props.apiKey || "",
            token: this.props.token || "",
            authorizing: false,
        };
    }

    render() {
        const {uid, apiKey, token} = this.state;
        return <div id="auth">
            <div>
                <em>Some application</em> wants to connect to your account.
            </div>
            <div>
                <button disabled={!(uid && apiKey && token)} onClick={() => {
                    this.onAuthorize(uid, apiKey, token);
                }
                }>Connect application
                </button>
            </div>
        </div>;
    }

    private onAuthorize(uid: string, apiKey: string, token: string) {
        if (uid && apiKey && token) {
            this.setState({
                authorizing: true,
            });
            firestore.collection("tokens").doc(token).update({validated: true});
        }
    }
}