import * as React from "react";
import {StatelessComponent} from "react";
import {Scrobble} from "../scrobbler/Scrobble";

export const MostRecentScrobblesSection: StatelessComponent<{ signedIn: boolean, scrobbles: Scrobble[] }> = ({scrobbles, signedIn}) =>
    <section className={`section-most-recent-scrobbles ${signedIn ? "signed-in" : "not-signed-in"}`}>
        <h2 className={`title-scrobbles`}>Most recent scrobbles</h2>
        <ol className={`list-most-recent-scrobbles`}>
            {
                scrobbles.length > 0 ? scrobbles.map((el, i) => <li
                    key={`scrobble-${i}`}>{el.artist.name} - {el.name} ({el.utc})</li>) : <div>No scrobbles yet!</div>
            }
        </ol>
    </section>;