export interface Scrobble {
    artist: {
        name: string,
        mbid?: string,
    };
    name: string;
    utc: number;
}

export const parseScrobble = (s: any): Scrobble => ({
    artist: {
        name: s.artistName as string,
        mbid: s.artistMbid as string,
    },
    name: s.name as string,
    utc: s.utc.seconds as number,
});