import * as React from "react";
import {PureComponent} from "react";
import {SignUp} from "./SignUp";
import {parseScrobble, Scrobble} from "../scrobbler/Scrobble";
import {MostRecentScrobblesSection} from "./MostRecentScrobblesSection";

const firebase: any = (window as any).firebase;
const firestore = firebase.firestore();
const settings = {timestampsInSnapshots: true};
firestore.settings(settings);

export class App extends PureComponent<{
    username: string,
    email: string,
    uid: string,
}, {
    signingIn: boolean;
    signUpOpen: boolean,
    username: string,
    email: string,
    uid: string,
    emailEntered: string,
    passwordEntered: string,
    paymentDetails?: any,
    mostRecentScrobbles: Scrobble[],
    paymentComplete?: PaymentComplete, // "success" | "fail" | "unknown"
}> {
    state = this.getInitialS();

    getInitialS() {
        if (firebase) {
            firebase.auth().onAuthStateChanged((u?: any) => {
                if (!!u && u.email) {
                    console.log("Signed in as " + (u.displayName || u.email));
                    const uid = u.uid;
                    this.setState({
                        email: u.email,
                        username: u.displayName || u.email,
                        uid: uid,
                        signingIn: false,
                    });
                    this.fetchMostRecentScrobbles(uid)
                        .then(scrobbles => this.setState({
                            mostRecentScrobbles: scrobbles,
                        }));
                } else {
                    this.setState({
                        email: "",
                        username: "",
                        uid: "",
                        signingIn: false,
                    });
                }
            });
            // todo listen for auth change
        }
        return {
            mostRecentScrobbles: [],
            signingIn: false,
            emailEntered: "",
            passwordEntered: "",
            username: this.props.username || "",
            email: this.props.email || "",
            uid: this.props.uid || "",
            paymentDetails: null,
            paymentComplete: null,
            signUpOpen: false,
        };
    }

    render() {
        return <div>
            <div className="landing-pad">
                <div className="table-row">
                    <div className={`landing ${this.state.uid ? "signed-in" : "not-signed-in"}`}>
                        <h1 className="app-title">Scrobble.fm</h1>
                        <p className="sales-talk">The scrobbling alternative that respects your privacy.</p>
                        <div className={`sign-up-container ${this.state.email ? "unvisible" : "visible"}`}>
                            <a className={`sign-up ${this.state.signUpOpen ? "open" : "closed"}`} href="#"
                               onClick={(e) => {
                                   e.preventDefault();
                                   this.setState({
                                       signUpOpen: !this.state.signUpOpen,
                                   });
                               }
                               }>Sign up for free</a>
                            <p className={`${this.state.signUpOpen ? "hidden" : "visible"} sign-up-subtext`}>Most recent
                                500
                                scrobbles are free.<br/>After that, you can choose to
                                upgrade
                                for $0.99/month.</p>
                        </div>
                        <SignUp
                            onRegister={(user, email) => this.setState({signUpOpen: false,})}
                            onSignIn={(user, email, uid) => this.setState({username: user, email, uid})}
                            onSignOut={() => this.setState({username: "", email: "", uid: ""})}
                            signUpOpen={this.state.signUpOpen}/>
                        <div className={`${this.state.paymentDetails ? "visible" : "hidden"}`}>Processing payment...
                        </div>
                        <div
                            className={`${this.state.paymentComplete ? "visible" : "hidden"}`}>{this.state.paymentComplete ? this.state.paymentComplete : ""}</div>
                    </div>
                </div>
            </div>
            <div className={`profile-details ${this.state.uid ? "visible" : "unvisible"}`}>
                <MostRecentScrobblesSection signedIn={!!this.state.uid} scrobbles={this.state.mostRecentScrobbles}/>
            </div>
            <button onClick={() => {
                this.setState({signingIn: true});
            }} className={`btn-log-in-init ${this.state.signingIn || this.state.email ? "hidden" : "visible"}`}>Sign in
            </button>
            <div className={`signed-in-box`}>
                <div className={`${this.state.username ? "visible" : "hidden"}`}>
                    Signed in as <em
                    className={`signed-in-username`}>{this.state.username}</em> <a href="#"
                                                                                   onClick={() => {
                                                                                       const firebase = (window as any).firebase;
                                                                                       if (firebase) {
                                                                                           firebase.auth().signOut();
                                                                                           this.setState({
                                                                                               username: "",
                                                                                               email: "",
                                                                                               uid: "",
                                                                                           });
                                                                                       }
                                                                                   }}
                                                                                   className="btn-log-out">Log out</a>
                </div>
                <div className={`${this.state.username ? "hidden" : "visible"} widget-sign-in-container`}>
                    <div className={`widget-sign-in`}>
                        <form onSubmit={(e) => {
                            e.preventDefault();

                            const firebase = (window as any).firebase;
                            if (firebase) {
                                firebase.auth().signInWithEmailAndPassword(
                                    this.state.emailEntered,
                                    this.state.passwordEntered,
                                ).then((u) => {
                                    // Signed in
                                }).catch((err) => {
                                    console.error(err);
                                });
                                this.setState({
                                    username: "",
                                    email: "",
                                    uid: "",
                                });
                            }
                        }
                        }>
                            <div className={`widget-sign-in-fields`}>
                                <input type="text" placeholder="Email address"
                                       className={` widget-sign-in-field ${this.state.signingIn ? "visible" : "hidden"}`}
                                       onChange={(e) => this.setState({emailEntered: e.target.value})}/>
                                <input type="password" placeholder="Password"
                                       className={` widget-sign-in-field ${this.state.signingIn ? "visible" : "hidden"}`}
                                       onChange={(e) => this.setState({passwordEntered: e.target.value})}/>
                            </div>
                            <div
                                className={`btn-log-in-holder ${this.state.passwordEntered && this.state.emailEntered && this.state.signingIn ? "visible" : "hidden"}`}>
                                <input type="submit"
                                       value="Sign in"
                                       className="btn-log-in"/>
                            </div>
                        </form>
                    </div>
                </div>
            </div>
        </div>;

    }

    // <button onClick={(e) => {
    //     if ((window as any).PaymentRequest) {
    //         requestPayment()
    //             .then((paymentResponse) =>
    //                 this.processPayment(paymentResponse)
    //                     .then(paymentComplete => {
    //                         // console.log(paymentComplete);
    //                         return paymentResponse.complete(paymentComplete);
    //                     }))
    //             .catch((err) => {
    //                 if (err.message !== "Request cancelled") console.error(err.message);
    //             });
    //     } else {
    //         alert("Payment is not supported in your browser. Please upgrade to a modern browser such as Chrome.");
    //         // Fallback to traditional checkout
    //         // window.location.href = '/checkout/traditional';
    //     }
    // }}>Make payment
    // </button>

    private processPayment(paymentResponse: PaymentResponse) {
        return new Promise<PaymentComplete>((res, rej) => {
            // The user filled in the required fields and completed the flow
            // Get the details from `paymentResponse` and complete the transaction.
            const paymentDetails = paymentResponse.details;
            console.log(JSON.stringify(paymentDetails));
            this.setState({
                paymentDetails: paymentResponse.details,
            });
            setTimeout(() => {
                res("success" as PaymentComplete);
            }, 1500);
        }).then((paymentComplete) => {
            this.setState({
                paymentDetails: null,
                paymentComplete,
            });
            return paymentComplete;
        });
    }

    private onPaymentCompleted(paymentResponse: PaymentComplete) {
        this.setState({
            paymentDetails: null,
        });
    }

    private fetchMostRecentScrobbles(uid: string) {
        return firestore
            .collection("scrobbles")
            .where("uid", "==", uid)
            .orderBy("utc", "desc")
            .limit(10)
            .get()
            .then((results) => results.docs.map(e => parseScrobble(e.data())));
    }
}