import * as React from "react";
import {PureComponent} from "react";
import {ApiParameter} from "./ApiParameter";

const usernameMinLength = 3;


export class SignUp extends PureComponent <{
    signUpOpen: boolean,
    onRegister: (user: string, email: string) => any,
    onSignIn: (user: string, email: string, uid: string) => any,
    onSignOut: () => any
}, {
    registering: boolean;
    registered: string;
    email: string;
    username: string;
    password: string;
    password2: string;
    errorCode: string;
    errorMessage: string;
}> {
    state = {
        registering: false,
        registered: "",
        username: "",
        email: "",
        password: "",
        password2: "",
        errorCode: "",
        errorMessage: "",
    };

    render() {
        return <div className={`${this.props.signUpOpen ? "visible" : "hidden"} sign-up-form`}>
            <ApiParameter defaultValue=""
                          currentValue={this.state.email}
                          title="Email address"
                          htmlFor="email"
                          onChange={(e) => {
                              this.setState({email: e.target.value});
                          }
                          }/>
            <ApiParameter defaultValue=""
                          currentValue={this.state.username && this.state.username.length > usernameMinLength}
                          title="Username"
                          htmlFor="username"
                          onChange={(e) => {
                              this.setState({username: e.target.value});
                          }
                          }/>
            <ApiParameter defaultValue=""
                          currentValue={this.state.password}
                          title="Password"
                          type="password"
                          htmlFor="password"
                          onChange={(e) => {
                              this.setState({password: e.target.value});
                          }
                          }/>
            <ApiParameter defaultValue=""
                          currentValue={this.passwordsCheckOut()}
                          title="Confirm password"
                          type="password"
                          htmlFor="password2"
                          onChange={(e) => {
                              this.setState({password2: e.target.value});
                          }
                          }/>
            <div
                className={`sign-up-status ${this.registeringOrRegistered() || this.state.errorMessage ? "visible" : "hidden"}`}>{
                this.state.errorMessage ? <span>{this.state.errorMessage}</span> :
                    this.state.registering ?
                        <span>Registering <b>{this.state.username}</b></span> : this.state.registered ?
                        <span>Registered <b>{this.state.registered}</b>. Check your e-mail to log in.</span> : ""}</div>
            <div
                className={`btn-sign-up-holder ${!this.registeringOrRegistered() && this.fieldsFilledInCorrectly() ? "visible" : "hidden"}`}>
                <button
                    disabled={this.registeringOrRegistered()}
                    onClick={() => this.register(this.state.username, this.state.email, this.state.password)
                        .catch((e: any) => {
                            console.error(e);
                            this.setState({
                                errorCode: e.code + "",
                                errorMessage: e.message + "",
                                registering: false,
                                registered: "",
                            });
                        })}
                    className={`btn-sign-up ${!this.registeringOrRegistered() ? "visible" : "hidden"}`}>
                    Register
                </button>
            </div>
        </div>;
    }

    private registeringOrRegistered(): boolean {
        return !!(this.state.registering || this.state.registered);
    }

    private passwordsCheckOut() {
        return this.state.password2 && this.state.password2 === this.state.password;
    }

    private fieldsFilledInCorrectly() {
        return this.state.username && this.state.username.length > usernameMinLength && this.state.password && this.passwordsCheckOut();
    }

    private onRegistered(username: string, email: string) {
        this.setState({
            registering: false,
            registered: username,
            errorMessage: "",
            errorCode: "",
        });
        this.props.onRegister(username, email);
    }

    private register(username: string, email: string, password: string) {
        this.setState({
            registering: true,
            errorMessage: "",
            errorCode: "",
        });
        const actionCodeSettings = {
            // URL you want to redirect back to. The domain (www.example.com) for this
            // URL must be whitelisted in the Firebase Console.
            url: `https://digitalheir.github.io/scrobble-fm/user?username=${username}`,
            // This must be true.
            handleCodeInApp: true,
            iOS: {
                bundleId: "net.blackenvelope.scrobblefm.ios"
            },
            android: {
                packageName: "net.blackenvelope.scrobblefm",
                installApp: true,
                minimumVersion: "1"
            }
        };
        const auth = (window as any).firebase.auth();
        return (auth.createUserWithEmailAndPassword(email, password) as Promise<any>)
            .then(() => {
                const u = auth.currentUser;
                u.updateProfile({
                    displayName: username,
                }).then(() => {
                    this.onRegistered(username, email);
                    // happens in listener
                    // this.props.onSignIn(username, email, uid);
                    // Save the email locally so you don't need to ask the user for it again
                    // if they open the link on the same device.
                    // todo use it actually
                    window.localStorage.setItem("emailForSignIn", email);
                }).catch(function (err) {
                    console.error(err);
                    u.delete()
                        .then(() => {
                            this.props.onSignOut();
                        });
                });
            });
    }
}